 font-weight: 400;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: center;\n  cursor: pointer;\n}\n.save-button[data-v-b223ba92] {\n  width: 72px;\n  height: 40px;\n  padding: 8px, 16px, 8px, 16px;\n  border-radius: 4px;\n  gap: 4px;\n  background: #151233;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 19px;\n  letter-spacing: 0px;\n  text-align: center;\n  color: #FFFFFF;\n}\n.back-button[data-v-b223ba92] {\n  width: 68px;\n  height: 40px;\n  padding: 8px 16px 8px 16px;\n  gap: 4px;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 19px;\n  letter-spacing: 0px;\n  text-align: center;\n  color: #757575;\n  background-color: transparent;\n  border: none;\n}\n.navigations[data-v-b223ba92] {\n  display: flex;\n  justify-content: flex-end;\n  padding-top: 24px;\n}\n.default-title[data-v-b223ba92] {\n  font-family: Roboto;\n  font-size: 15px;\n  font-weight: 500;\n  line-height: 18px;\n  letter-spacing: 0em;\n  text-align: left;\n  margin-top: 24px;\n  margin-bottom: 8px;\n  color: #151233;\n}\n.add-text[data-v-b223ba92] {\n  margin-right: 4px;\n  color: #151233;\n}\n.add-theme-icon[data-v-b223ba92] {\n  font-family: Material Design Icons;\n  font-size: 24px;\n  font-weight: 400;\n  line-height: 24px;\n  letter-spacing: 0em;\n  text-align: center;\n  margin-right: 10px;\n}\n.slide-fade-enter-active[data-v-b223ba92] {\n  transition: all 0.2s ease-out;\n}\n.slide-fade-leave-active[data-v-b223ba92] {\n  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);\n}\n.slide-fade-enter-from[data-v-b223ba92],\n.slide-fade-leave-to[data-v-b223ba92] {\n  transform: translateX(20px);\n  opacity: 0;\n}\n.theme-desc[data-v-b223ba92] {\n  font-size: 16px;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: left;\n  color: #616161;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/ThemesSettings.vue"],"names":[],"mappings":";AAsQA;EACE,eAAe;EACf,iBAAiB;AACnB;AAEA;E