rt { IAppSettings } from \"../../../../../../common/qCloudTemp/application\";\nimport ThemeCard from \"./Theme/ThemeCard.vue\";\nimport ThemeConfiguration from \"./Theme/ThemeConfiguration.vue\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\nimport { container } from \"../../../../domain/core/diContainer\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n\nenum ShowState {\n  ThemeNames = 1,\n  ThemeConfiguration = 2\n}\nconst DEFAULTTHEMENAME = \"Theme1\";\ninterface IThemeSettingsProp {\n  themeSettings: IAppSettings['themeSettings']\n}\nconst props = defineProps<IThemeSettingsProp>();\nconst emit = defineEmits([\"onSave\", \"hideLeftNav\", \"showLeftNav\", \"showHeader\", \"hideHeader\"]);\nconst themeDisplayState = ref(ShowState.ThemeNames);\nconst selectedThemeName = ref(props.themeSettings?.defaultTheme || DEFAULTTHEMENAME);\nconst title = ref(\"Themes Settings\")\nconst editTheme = ref<ThemeOptions>({ colors: [], fonts: [] });\nconst dialog = container.resolve<IDialog>(IDialog);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nonMounted(() => {\n  title.value = \"Themes Settings\";\n  emit('hideHeader');\n});\nfunction getColors() {\n  const preDefinedColors: Array<IThemeColor> = ([{ name: \"Background\", allowGradient: true, color: \"#FFFFFF\" },\n  { name: \"Text\", allowGradient: false, color: \"#212121\" },\n  { name: \"Primary\", allowGradient: false, color: \"#151233\" },\n  { name: \"Secondary\", allowGradient: false, color: \"#FD454C\" },\n  { name: \"Tertiary\", allowGradient: false, color: \"#007C77\" },\n  { name: \"Passive\", allowGradient: false, color: \"#7A95B0\" },\n  { name: \"Info\", allowGradient: false, color: \"#03A9F4\" },\n  { name: \"Success\", allowGradient: false, color: \"#00BB41\" },\n  { name: \"Warning\", allowGradient: false, color: \"#F3CA40\" },\n  { name: \"Error\", allowGradient: false, color: \"#F44336\" }]);\n  return preDefinedColors;\n}\nfunction getFonts() {\n  const defaultFonts = getDefaultFonts();\n  const definedFonts: Array<IThemeFont> = [\n    {\n      fontClassName: \"Default\",\n      configs: {\n        fontFamily: \"Roboto\", fontColor: \"#000000\", fontSize: \"16px\", displayWeight: \"Regular - 400\", fontWeight: \"400\", combinedFont: \"Roboto-Regular\", fontStyle: \"normal\",\n        fontOptions: defaultFonts\n      }\n    },\n    {\n      fontClassName: \"Heading 1\",\n      configs: {\n        fontFamily: \"Raleway\", fontColor: \"#000000\", fontSize: \"24px\", displayWeight: \"Regular - 400\", fontWeight: \"400\", combinedFont: \"Raleway-Regular\", fontStyle: \"normal\",\n        fontOptions: defaultFonts\n      }\n    },\n    {\n      fontClassName: \"Heading 2\",\n      configs: {\n        fontFamily: \"Roboto Italic\", fontColor: \"#000000\", fontSize: \"16px\", displayWeight: \"Regular - 400\", fontWeight: \"400\", combinedFont: \"Roboto-RegularItalic\", fontStyle: \"Italic\",\n        fontOptions: defaultFonts\n      }\n    },\n    {\n      fontClassName: \"Heading 3\",\n      configs: {\n        fontFamily: \"Roboto\", fontColor: \"#000000\", fontSize: \"12px\", displayWeight: \"Regular - 400\", fontWeight: \"400\", combinedFont: \"Roboto-Regular\", fontStyle: \"normal\",\n        fontOptions: defaultFonts\n      }\n    },\n    {\n      fontClassName: \"Paragraph\",\n      configs: {\n        fontFamily: \"Roboto\", fontColor: \"#000000\", fontSize: \"12px\", displayWeight: \"Regular - 400\", fontWeight: \"400\", combinedFont: \"Roboto-Regular\", fontStyle: \"normal\",\n        fontOptions: defaultFonts\n      }\n    },\n    {\n      fontClassName: \"Info\",\n      configs: {\n        fontFamily: \"Roboto\", fontColor: \"#000000\", fontSize: \"12px\", displayWeight: \"Regular - 400\", fontWeight: \"400\", combinedFont: \"Roboto-Regular\", fontStyle: \"normal\",\n        fontOptions: defaultFonts\n      }\n    }];\n  return definedFonts;\n}\n\nfunction generateDefaultTheme() {\n  const defaultTheme: ITheme = { name: DEFAULTTHEMENAME, lightTheme: { colors: getColors(), fonts: getFonts() } }\n  return defaultTheme;\n}\n\nconst themes = ref(props.themeSettings?.themes || [generateDefaultTheme()]);\n\nfunction changeDisplay(state: number, titleName?: string) {\n  themeDisplayState.value += state\n  switch (themeDisplayState.value) {\n    case ShowState.ThemeNames:\n      title.value = \"Themes Settings\";\n      emit(\"showLeftNav\");\n      break;\n    case ShowState.ThemeConfiguration:\n      title.value = \"Theme Configuration - \" + titleName;\n      emit(\"hideLeftNav\")\n      break;\n    default:\n      break;\n  }\n  emit(\"hideHeader\")\n}\n\nfunction findNewString(themeName: string) {\n  let emptyNumber = themes.value.map(theme => {\n    let match = theme.name.match(/Theme(\\d+)$/);\n    return match ? parseInt(match[1]) : 0;\n  });\n\n  let numberToBeAdd: any = themeName.match(/\\d+$/);\n  numberToBeAdd = numberToBeAdd ? parseInt(numberToBeAdd[0]) : 1;\n\n  while (emptyNumber.includes(numberToBeAdd)) {\n    numberToBeAdd++;\n  }\n  return `Theme${numberToBeAdd}`;\n}\n\nfunction addNewTheme() {\n  const defaultTheme = generateDefaultTheme();\n\n  let defaultName = \"Theme\";\n  const themeName = findNewString(defaultName);\n  themes.value.push({ name: themeName, lightTheme: defaultTheme.lightTheme, darkTheme: defaultTheme.darkTheme });\n}\n\nfunction onEditTheme(themeOptions: ThemeOptions, title: string) {\n  editTheme.value = themeOptions;\n  changeDisplay(1, title);\n}\n\nfunction setDefaultTheme(themeName: string) {\n  selectedThemeName.value = themeName;\n}\n\nfunction onSaveTheme() {\n  emit(\"onSave\", { defaultTheme: selectedThemeName.value, themes: themes.value })\n}\n\nfunction deleteTheme(themeName: string) {\n  const deleteThemeIndex = themes.value.findIndex(theme => theme.name == themeName)!;\n  const deletedThemeName = themes.value[deleteThemeIndex].name\n  themes.value.splice(deleteThemeIndex, 1);\n  if (deletedThemeName == selectedThemeName.value) {\n    selectedThemeName.value = themes.value[0].name;\n  }\n}\n\nfunction addDarkTheme(theme: ITheme) {\n  const colors = getColors();\n  colors[0].color = \"#151233\";\n  theme.darkTheme = { fonts: getFonts(), colors }\n}\n\nfunction renameTheme(theme: ITheme) {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../../components/dialogs/renameTheme.vue`)),\n    {\n      closable: true, title: \"Edit Theme\", width: \"577px\", maxHeight: \"266px\", closeCb(reason) {\n        if (reason != \"close\") {\n          theme.name = reason;\n        }\n      },\n    },\n    {\n      name: theme.name,\n    },\n  );\n}\n</script>\n\n<template>\n  <div>\n    <div class=\"theme-content\">\n      <div class=\"title\">{{ title }}</div>\n      <div class=\"theme-desc\">\n        <span>Some Text here to describe</span>\n      </div>\n      <Transition name=\"slide-fade\">\n        <div v-if=\"themeDisplayState == ShowState.ThemeNames\">\n          <div v-for=\"theme in themes\" :key=\"theme.name\"\n            :class=\"['theme-name-div', 'row', selectedThemeName == theme.name ? 'selected-theme' : '']\">\n            <div class=\"theme-name-info\">\n              <span class=\"theme-title\">{{ theme.name }}</span>\n              <i class=\"mdi mdi-dots-horizontal horizontal-dot-icon\" id=\"dropdownMenuManage\" data-bs-toggle=\"dropdown\"\n                aria-expanded=\"false\" data-bs-target=\"#dropdown-menu\"></i>\n              <ul class=\"dropdown-menu user-info-dropwdown\" aria-labelledby=\"dropdownMenuButton1\">\n                <li>\n                  <div class=\"menu-item\" @click=\"setDefaultTheme(theme.name)\">\n                    <i class=\"mdi mdi-pencil\"></i>\n                    <a>Select as Default</a>\n                  </div>\n                </li>\n                <li>\n                  <div class=\"menu-item\" @click=\"renameTheme(theme)\">\n                    <i class=\"mdi mdi-pencil\"></i>\n                    <a>Rename</a>\n                  </div>\n                </li>\n                <li>\n                  <div class=\"menu-item\" @click=\"deleteTheme(theme.name)\">\n                    <i class=\"mdi mdi-delete\"></i>\n                    <a>Delete</a>\n                  </div>\n                </li>\n              </ul>\n            </div>\n            <div class=\"col-6\" style=\"padding-right: 6px;\">\n              <ThemeCard themeName=\"Light\" :theme=\"theme.lightTheme\"\n                @pencilClick=\"onEditTheme(theme.lightTheme, 'Light Theme of ' +  theme.name)\">\n              </ThemeCard>\n            </div>\n            <div class=\"col-6\" style=\"padding-left: 6px;\" v-if=\"!theme.darkTheme\">\n              <div class=\"add-theme-card\" @click=\"addDarkTheme(theme)\">\n                <i class=\"mdi mdi-plus add-theme-icon\"></i>\n                <span class=\"add-text\">Add</span>\n                <span>a dark theme</span>\n              </div>\n            </div>\n            <div class=\"col-6\" style=\"padding-left: 6px;\" v-else>\n              <ThemeCard themeName=\"Dark\" :theme=\"theme.darkTheme\"\n                @pencilClick=\"onEditTheme(theme.darkTheme, 'Dark Theme of ' +  theme.name)\">\n              </ThemeCard>\n            </div>\n          </div>\n          <div style=\"display: flex; justify-content: center; margin-top: 24px;\">\n            <button class=\"add-color-btn\" style=\"font-size: 16px\" @click=\"addNewTheme\">\n              <i class=\"mdi mdi-plus add-theme-icon\"></i>\n              Add New Theme\n            </button>\n          </div>\n        </div>\n      </Transition>\n      <Transition name=\"slide-fade\">\n        <div v-if=\"themeDisplayState == ShowState.ThemeConfiguration\" class=\"themes-configuration\">\n          <ThemeConfiguration :theme=\"editTheme\" @onBack=\"changeDisplay(-1)\" @onSaveTheme=\"onSaveTheme\">\n          </ThemeConfiguration>\n        </div>\n      </Transition>\n    </div>\n\n    <div v-if=\"themeDisplayState != ShowState.ThemeNames\" class=\"navigations\">\n      <button class=\"back-button\" @click=\"changeDisplay(-1)\">Back</button>\n      <button class=\"save-button\" @click=\"onSaveTheme\">SAVE</button>\n    </div>\n  </div>\n</template>\n\n<style scoped>\n.menu-item i {\n  font-size: 20px;\n  margin-right: 8px;\n}\n\n.menu-item:hover {\n  background-color: #d5d5d5 !important;\n  cursor: pointer;\n}\n\n.menu-item {\n  font-size: 14px;\n  line-height: 16px;\n  color: #212121;\n  display: flex;\n  align-items: center;\n  padding: 8px 16px 8px 12px;\n}\n\n.user-info-dropwdown {\n  width: 179px;\n  border: 1px solid #E3E3E3;\n  border-radius: 8px;\n  padding-bottom: 10px;\n}\n\n.theme-title {\n  font-family: Roboto;\n  font-size: 15px;\n  font-weight: 500;\n  text-align: left;\n  color: #151233;\n}\n\n.selected-theme {\n  border-width: 2px !important;\n  border-color: #151515ab !important;\n}\n\n.add-color-btn {\n  height: 32px;\n  padding: 8px 16px 8px 16px;\n  font-family: Roboto;\n  font-size: 14px;\n  font-weight: 500;\n  line-height: 16px;\n  letter-spacing: 0px;\n  text-align: center;\n  color: #757575;\n  background: transparent;\n  border: none;\n  display: flex;\n  align-items: center;\n}\n\n.theme-name-info {\n  display: flex;\n  justify-content: space-between;\n}\n\n.theme-name-div {\n  border-radius: 8px;\n  padding: 12px 0;\n  border: 1px solid #E3E3E3;\n  margin: 1px;\n  margin-top: 24px;\n}\n\n.theme-content::-webkit-scrollbar {\n  display: none;\n}\n\n.theme-content {\n  height: 640px;\n  overflow-y: scroll;\n  overflow-x: hidden;\n}\n\n.title {\n  font-style: normal;\n  font-weight: 500;\n  font-size: 20px;\n  line-height: 23px;\n  white-space: nowrap;\n  color: #151233;\n  margin-top: 40px;\n  margin-bottom: 8px;\n}\n\n.add-theme-card {\n  display: flex;\n  align-items: center;\n  vertical-align: middle;\n  border: 1px dashed #BCBCBC;\n  height: 115px;\n  justify-content: center;\n  border-radius: 10px;\n  color: #BCBCBC;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 400;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: center;\n  cursor: pointer;\n}\n\n.save-button {\n  width: 72px;\n  height: 40px;\n  padding: 8px, 16px, 8px, 16px;\n  border-radius: 4px;\n  gap: 4px;\n  background: #151233;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 19px;\n  letter-spacing: 0px;\n  text-align: center;\n  color: #FFFFFF;\n\n}\n\n.back-button {\n  width: 68px;\n  height: 40px;\n  padding: 8px 16px 8px 16px;\n  gap: 4px;\n  font-family: Roboto;\n  font-size: 16px;\n  font-weight: 500;\n  line-height: 19px;\n  letter-spacing: 0px;\n  text-align: center;\n  color: #757575;\n  background-color: transparent;\n  border: none;\n}\n\n.navigations {\n  display: flex;\n  justify-content: flex-end;\n  padding-top: 24px;\n}\n\n.default-title {\n  font-family: Roboto;\n  font-size: 15px;\n  font-weight: 500;\n  line-height: 18px;\n  letter-spacing: 0em;\n  text-align: left;\n  margin-top: 24px;\n  margin-bottom: 8px;\n  color: #151233;\n}\n\n.add-text {\n  margin-right: 4px;\n  color: #151233;\n}\n\n.add-theme-icon {\n  font-family: Material Design Icons;\n  font-size: 24px;\n  font-weight: 400;\n  line-height: 24px;\n  letter-spacing: 0em;\n  text-align: center;\n  margin-right: 10px;\n}\n\n.slide-fade-enter-active {\n  transition: all 0.2s ease-out;\n}\n\n.slide-fade-leave-active {\n  transition: all 0.5s cubic-bezier(1, 0.5, 0.8, 1);\n}\n\n.slide-fade-enter-from,\n.slide-fade-leave-to {\n  transform: translateX(20px);\n  opacity: 0;\n}\n\n.theme-desc {\n  font-size: 16px;\n  line-height: 19px;\n  letter-spacing: 0em;\n  text-align: left;\n  color: #616161;\n}\n</style>"],"sourceRoot":""}]);
