;IACd,eAAe;AACnB;AAEA;IACI,aAAa;AACjB;AAEA;IACI,gCAAgC;AACpC;AAEA;IACI,+BAA+B;AACnC;AAEA;IACI,aAAa;AACjB;AAEA;IACI,YAAY;AAChB","sourcesContent":["<script setup lang=\"ts\">\nimport { computed, ref } from \"vue\";\nimport { ThemeOptions } from \"@stechquick/algae/lib/quick/IPlateauUI\";\ninterface IThemeCardProp {\n    theme: ThemeOptions,\n    themeName: string\n}\n\nconst props = defineProps<IThemeCardProp>();\nconst emit = defineEmits(['cardClick', 'pencilClick']);\n\nconst descriptionText = computed(() => {\n    return `${props.theme.fonts[0].configs.fontFamily}, ${props.theme.fonts[1].configs.fontFamily}, ${props.theme.fonts[2].configs.fontFamily} + ${props.theme.fonts.length - 4}`\n})\n\nconst bgColor = ref(props.theme.colors.find(color => color.name == \"Background\")?.color)\nconst subDivColors = computed(() => {\n    const colorArr: { [key: string]: string } = {}\n    colorArr.errorColor = props.theme.colors.find(color => color.name == \"Error\")?.color || \"\"\n    colorArr.successColor = props.theme.colors.find(color => color.name == \"Success\")?.color || \"\"\n    colorArr.primaryColor = props.theme.colors.find(color => color.name == \"Primary\")?.color || \"\"\n    colorArr.infoColor = props.theme.colors.find(color => color.name == \"Info\")?.color || \"\"\n    colorArr.warningColor = props.theme.colors.find(color => color.name == \"Warning\")?.color || \"\"\n    return Object.keys(colorArr).map(key => colorArr[key]);\n})\nconst isDarkTheme = ref(props.themeName == \"Dark\")\n\n</script>\n\n<template>\n    <div class=\"theme-card\" :style=\"{ background: bgColor }\">\n        <div class=\"actions\">\n            <div class=\"theme-title\">\n                <span :class=\"['title', isDarkTheme ? 'dark' : '']\">{{ themeName }}</span>\n                <span :class=\"['description', isDarkTheme ? 'dark' : '']\">{{ descriptionText }}</span>\n            </div>\n            <div class=\"action-buttons\">\n                <i :class=\"['mdi mdi-pencil-outline', , isDarkTheme ? 'dark' : '']\" :style=\"{}\"\n                    @click=\"emit('pencilClick', themeName)\"></i>\n            </div>\n        </div>\n        <div class=\"theme-colors\">\n            <div v-for=\"color in subDivColors\" :key=\"color\" :style=\"{ backgroundColor: color }\"\n                class=\"buttom-color col\"></div>\n        </div>\n    </div>\n</template>\n\n<style scoped>\n.dark {\n    color: white !important;\n}\n\n.actions {\n    display: flex;\n    justify-content: space-between;\n    cursor: pointer;\n    padding: 20px 24px;\n}\n\n.themes-section .action-buttons i {\n    font-size: 20px;\n    color: #757575;\n}\n\n.action-buttons {\n    display: flex;\n    justify-content: space-around;\n    gap: 12px;\n}\n\n\n.theme-card {\n    height: 115px;\n    border-radius: 10px;\n    border: 1px #E3E3E3 solid;\n    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12);\n}\n\n.theme-card .title {\n    font-family: Roboto;\n    font-size: 20px;\n    font-weight: 500;\n    line-height: 23px;\n    letter-spacing: 0em;\n    text-align: left;\n    color: #212121;\n}\n\n.theme-card .description {\n    font-family: Roboto;\n    font-size: 16px;\n    font-weight: 400;\n    line-height: 20px;\n    letter-spacing: 0em;\n    text-align: left;\n    color: #757575;\n    margin-top: 8px;\n}\n\n.theme-title {\n    display: grid;\n}\n\n.buttom-color:last-of-type {\n    border-bottom-right-radius: 10px;\n}\n\n.buttom-color:nth-of-type(1) {\n    border-bottom-left-radius: 10px;\n}\n\n.theme-colors {\n    display: flex;\n}\n\n.buttom-color {\n    height: 24px;\n}\n</style>"],"sourceRoot":""}]);
