___CSS_LOADER_EXPORT___.push([module.id, "\n.custom-font-title[data-v-684f9c8e] {\n    font-family: Roboto;\n    font-size: 15px;\n    font-weight: 500;\n    line-height: 17.58px;\n    text-align: left;\n    color: #151233;\n}\n.custom-fonts[data-v-684f9c8e] {\n    margin-top: 24px;\n}\n.tab-item[data-v-684f9c8e] {\n    width: 149px;\n    height: 36px;\n    padding: 10px 54px 10px 54px;\n    font-family: Roboto;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 16px;\n    letter-spacing: 0em;\n    text-align: center;\n    color: #A1A1A1;\n}\n.config-tab[data-v-684f9c8e] {\n    display: flex;\n    align-items: center;\n    margin: 32px 0 24px;\n    border-bottom: 1px solid var(--grey-lighten-1, #BDBDBD);\n}\n.active-category[data-v-684f9c8e] {\n    color: #151233;\n    border-bottom: 2px solid;\n    font-family: Roboto;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 16px;\n    letter-spacing: 0em;\n    text-align: center;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/application/Theme/ThemeConfiguration.vue"],"names":[],"mappings":";AA2HA;IACI,mBAAmB;IACnB,eAAe;IACf,gBAAgB;IAChB,oBAAoB;IACpB,gBAAgB;IAChB,cAAc;AAClB;AAEA;IACI,gBAAgB;AACpB;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,4BAA4B;IAC5B,mBAAmB;IACnB,eAAe;IACf,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,kBAAkB;IAClB,cAAc;AAElB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,mBAAmB;IACnB,uDAAuD;AAC3D;AAEA;IACI,cAAc;IACd,wBAAwB;IACxB,mBAAmB;IACnB,eAAe;IACf,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,kBAAkB;AACtB","sourcesContent":["<script setup lang=\"ts\">\nimport { FontConfigs, IThemeColor, IThemeFont, ThemeOptions } from \"@stechquick/algae/lib/quick/IPlateauUI\";\nimport { ref } from \"vue\";\n\nimport ColorConfig from './ColorConfig.vue'\nimport FontConfig from './FontConfig.vue'\nimport { getDefaultFonts } from \"@stechquick/shrimp/lib/interfaces/ComponentInterfaces/IStyle\";\n\nenum TabState {\n    General,\n    Colors,\n    Fonts\n}\n\ninterface IThemeConfigProps {\n    theme: ThemeOptions\n}\n\nconst activeTab = ref<TabState>(TabState.Colors);\nconst props = defineProps<IThemeConfigProps>();\nconst emit = defineEmits([\"onBack\", \"onSaveTheme\"]);\nconst customColors = ref(props.theme.colors.slice(10));\nconst colorInputs = ref<{ [key: string]: any }>({});\nconst customFonts = ref(props.theme.fonts.slice(6))\n\n\nfunction onColorChange(value: string, colorName: string) {\n    const selectedColor = props.theme.colors.find(color => color.name == colorName)!;\n    selectedColor.color = value;\n}\n\nfunction addNewColor() {\n    const nameIndex = customColors.value.length;\n    const generateRandomColor = () => {\n        const randomInt = Math.floor(Math.random() * 16777216);\n        const hexColor = `#${randomInt.toString(16).padStart(6, '0')}`;\n        return hexColor;\n    };\n    // const color = generateRandomColor();\n    const color = \"\";\n\n    const customColor: IThemeColor = { name: \"DefaultColor\" + nameIndex, allowGradient: true, color }\n    customColors.value.push(customColor);\n    props.theme.colors.push(customColor);\n}\n\nfunction onColorCardClick(colorName: string) {\n    (<any>colorInputs.value[colorName])?.clickColorPicker();\n}\n\nfunction assignRefs(colorName: string, element: any) {\n    colorInputs.value[colorName] = element\n}\nfunction onCustomColorDelete(colorName: string) {\n    const colorIndex = customColors.value.findIndex(color => color.name == colorName)!\n    customColors.value.splice(colorIndex, 1);\n    props.theme.colors.splice(10 + colorIndex, 1);\n}\n\nfunction calcPickerStyle(index: number) {\n    return { left: index > 7 ? \"-210px\" : \"20px\", top: \"-80px\" }\n}\n\nfunction isColorValid(colorCode: string) {\n    const regex = /^#[0-9A-Fa-f]{3}$|^#[0-9A-Fa-f]{6}$/i;\n    const isValidColorCode = regex.test(colorCode);\n    return isValidColorCode;\n}\nfunction findNewString(fontClasName: string) {\n    let emptyNumber = customFonts.value.map(font => {\n        let match = font.fontClassName.match(/Type(\\d+)$/);\n        return match ? parseInt(match[1]) : 0;\n    });\n\n    let numberToBeAdd: any = fontClasName.match(/\\d+$/);\n    numberToBeAdd = numberToBeAdd ? parseInt(numberToBeAdd[0]) : 1;\n\n    while (emptyNumber.includes(numberToBeAdd)) {\n        numberToBeAdd++;\n    }\n    return `Type${numberToBeAdd}`;\n}\nfunction deleteFont(fontClassName: string) {\n    const fontIndex = customFonts.value.findIndex(font => font.fontClassName == fontClassName);\n    customFonts.value.splice(fontIndex, 1);\n    props.theme.fonts.splice(6 + fontIndex, 1);\n}\n\nfunction addFont() {\n    const newFontClassName = findNewString(\"Type\");\n    const newConfigs: FontConfigs = { fontFamily: \"Roboto\", combinedFont: \"Roboto-Regular\", fontColor: \"#000000\", fontSize: \"16px\", displayWeight: \"Regular - 400\", fontWeight: \"400\", fontOptions: getDefaultFonts(), fontStyle: \"normal\" };\n    const newFont: IThemeFont = { fontClassName: newFontClassName, configs: newConfigs, showDelete: true };\n    customFonts.value.push(newFont);\n    props.theme.fonts.push(newFont);\n}\n</script>\n\n<template>\n    <div class=\"config-div\">\n        <div class=\"config-tab\">\n            <!-- <div @click=\"activeTab = TabState.General;\"\n                :class=\"['tab-item', (activeTab == TabState.General ? 'active-category' : '')]\">General</div> -->\n            <div @click=\"activeTab = TabState.Colors;\"\n                :class=\"['tab-item', (activeTab == TabState.Colors ? 'active-category' : '')]\">Colors</div>\n            <div @click=\"activeTab = TabState.Fonts;\"\n                :class=\"['tab-item', (activeTab == TabState.Fonts ? 'active-category' : '')]\">Fonts</div>\n        </div>\n        <!-- <template v-if=\"activeTab == TabState.General\">\n        </template> -->\n        <template v-if=\"activeTab == TabState.Colors\">\n            <ColorConfig :onColorCardClick=\"onColorCardClick\" :onColorChange=\"onColorChange\"\n                :colors=\"props.theme.colors.slice(0, 10)\" :calcPickerStyle=\"calcPickerStyle\" :assignRefs=\"assignRefs\"\n                :customColors=\"customColors\" :onCustomColorDelete=\"onCustomColorDelete\" :addNewColor=\"addNewColor\" />\n        </template>\n\n        <template v-if=\"activeTab == TabState.Fonts\">\n            <FontConfig :fonts=\"props.theme.fonts.slice(0, 6)\" :customFonts=\"customFonts\" @addFont=\"addFont\"\n                @deleteFont=\"deleteFont\" />\n        </template>\n    </div>\n</template>\n\n<style scoped>\n.custom-font-title {\n    font-family: Roboto;\n    font-size: 15px;\n    font-weight: 500;\n    line-height: 17.58px;\n    text-align: left;\n    color: #151233;\n}\n\n.custom-fonts {\n    margin-top: 24px;\n}\n\n.tab-item {\n    width: 149px;\n    height: 36px;\n    padding: 10px 54px 10px 54px;\n    font-family: Roboto;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 16px;\n    letter-spacing: 0em;\n    text-align: center;\n    color: #A1A1A1;\n\n}\n\n.config-tab {\n    display: flex;\n    align-items: center;\n    margin: 32px 0 24px;\n    border-bottom: 1px solid var(--grey-lighten-1, #BDBDBD);\n}\n\n.active-category {\n    color: #151233;\n    border-bottom: 2px solid;\n    font-family: Roboto;\n    font-size: 14px;\n    font-weight: 500;\n    line-height: 16px;\n    letter-spacing: 0em;\n    text-align: center;\n}\n</style>"],"sourceRoot":""}]);
