CZ;AAEA;CACC,WAAW;AACZ;AAEA;CACC,WAAW;AACZ","sourcesContent":["<script setup lang=\"ts\">\nimport { computed, ref } from \"vue\";\nimport { IThemeFont } from \"@stechquick/algae/lib/quick/IPlateauUI\";\nimport { getDefaultFonts, FontWeights } from \"@stechquick/shrimp/lib/interfaces/ComponentInterfaces/IStyle\";\nimport CustomSelect from \"../../customSelect.vue\";\nimport ColorInput from \"./ColorInput.vue\";\n\nconst props = defineProps<{\n\tfont: IThemeFont;\n}>()\nconst fontOptions = ref(getDefaultFonts().map(option => option.fontFamily));\nconst fontWeights = ref(getFontWeights(props.font));\nconst weightRefs = ref<{ [key: string]: any }>({});\nconst currentFontWeight = computed(() => {\n\treturn props.font.configs.displayWeight;\n})\n\nconst currentFontColor = computed(() => {\n\treturn props.font.configs.fontColor;\n})\n\nconst emit = defineEmits<{\n\t(name: \"deleteFont\", fontClassName: string): void\n}>();\n\nfunction updateFontName(font: IThemeFont, fontFamily: string) {\n\tfont.configs.fontFamily = fontFamily;\n\tfont.configs.fontStyle = fontFamily.toLocaleLowerCase().includes(\"italic\") ? \"Italic\" : \"normal\";\n\tupdateFontWeights(font);\n}\n\nfunction getFontWeights(font: IThemeFont) {\n\treturn font.configs.fontOptions.find(fontOption => fontOption.fontFamily == font.configs.fontFamily)!.fontWeights;\n}\nfunction updateFontWeights(font: IThemeFont, fontValue?: string) {\n\tfontWeights.value = font.configs.fontOptions.find(item => item.fontFamily == font.configs.fontFamily)!.fontWeights;\n\tfont.configs.displayWeight = fontValue ?? font.configs.displayWeight;\n\tif (!fontWeights.value.includes(font.configs.displayWeight)) {\n\t\tfont.configs.displayWeight = FontWeights[FontWeights[\"Regular - 400\"]]\n\t}\n\tweightRefs.value[font.fontClassName].setSelected(font.configs.displayWeight);\n}\n\nfunction updateFontSize(fontSize: string, value?: string) {\n\tif (!value) {\n\t\treturn;\n\t}\n\tprops.font.configs.fontSize = value;\n\tfontSize = value;\n}\n\nfunction updateFontColor(fontColor: string, value?: string) {\n\tif (!value) {\n\t\treturn;\n\t}\n\tprops.font.configs.fontColor = value;\n\tfontColor = value;\n}\n\nfunction updateFontClassName(className: string, value?: string) {\n\tif (!value) {\n\t\treturn;\n\t}\n\tprops.font.fontClassName = value;\n\tclassName = value;\n}\n\n// function updatefontStyle() {\n// \tprops.font.configs.fontStyle = \n// }\n\nfunction assignRefs(fontClassName: string, element: any) {\n\tweightRefs.value[fontClassName] = element\n}\n</script>\n\n<template>\n\t<div class=\"font-configuration\">\n\t\t<div class=\"font-class-name\" v-if=\"!font.showDelete\">{{ font.fontClassName }}</div>\n\t\t<div class=\"custom-font-name\" v-else>\n\t\t\t<input class=\"color-input\" type=\"text\" v-model=\"font.fontClassName\"\n\t\t\t\t@change=\"updateFontClassName(font.fontClassName, $event.target?.value)\">\n\t\t</div>\n\t\t<div class=\"font-name\">\n\t\t\t<CustomSelect :options=\"fontOptions\" class=\"custom-font-select\" :default=\"font.configs.fontFamily\"\n\t\t\t\t:placeholder=\"'Select'\" @input=\"updateFontName(font, $event)\"></CustomSelect>\n\t\t</div>\n\t\t<div class=\"font-weight\">\n\t\t\t<CustomSelect :options=\"fontWeights\" class=\"custom-font-select\" :default=\"currentFontWeight\"\n\t\t\t\t:placeholder=\"'Select'\" :ref=\"el => assignRefs(font.fontClassName, el)\"\n\t\t\t\t@input=\"updateFontWeights(font, $event)\">\n\t\t\t</CustomSelect>\n\t\t</div>\n\t\t<div class=\"font-size\">\n\t\t\t<input class=\"color-input\" style=\"width: 48px;\" type=\"text\" v-model=\"font.configs.fontSize\"\n\t\t\t\t@change=\"updateFontSize(font.configs.fontSize, $event.target?.value)\">\n\t\t</div>\n\t\t<div class=\"font-color-text\">\n\t\t\t<input class=\"color-input\" style=\"width: 90px;\" type=\"text\" v-model=\"currentFontColor\"\n\t\t\t\t@change=\"updateFontColor(currentFontColor, $event.target?.value)\">\n\t\t</div>\n\t\t<div class=\"font-color\">\n\t\t\t<ColorInput :color=\"currentFontColor\" :showColorDiv=\"true\" :pickerStyle=\"{ left: '-200px' }\"\n\t\t\t\t@onChangeColor=\"updateFontColor(currentFontColor, $event)\">\n\t\t\t</ColorInput>\n\t\t</div>\n\t\t<div class=\"delete-sec\" v-if=\"font.showDelete\">\n\t\t\t<i class=\"delete-icon mdi mdi-delete-outline\" @click=\"emit('deleteFont', font.fontClassName)\"></i>\n\t\t</div>\n\t</div>\n</template>\n\n<style scoped>\n.delete-icon {\n\tfont-family: Material Design Icons;\n\tfont-size: 16px;\n\tfont-weight: 400;\n\tline-height: 16px;\n\tletter-spacing: 0em;\n\ttext-align: center;\n\tcolor: #757575;\n\tmargin-left: 6px;\n\tcursor: pointer;\n}\n\n.font-configuration {\n\tdisplay: flex;\n\tgap: 12px;\n\talign-items: center;\n\tmargin-top: 12px;\n}\n\n.custom-font-select {\n\twidth: 100%;\n\tfont-size: 15px;\n\theight: 40px;\n\tline-height: 40px;\n\tborder: 1px solid #BCBCBC;\n\tborder-radius: 4px;\n\tpadding: 0px 12px;\n}\n\n.font-class-name {\n\twidth: 180px;\n\tcolor: #616161;\n\tfont-family: Roboto;\n\tfont-size: 16px;\n\tfont-weight: 400;\n\tline-height: 18.75px;\n}\n\n.color-input {\n\tfont-family: Roboto;\n\tfont-size: 12px;\n\tfont-weight: 400;\n\tline-height: 14px;\n\tletter-spacing: 0em;\n\ttext-align: left;\n\tcolor: #212121;\n\tpadding: 7px 6px 7px 6px;\n\tborder-radius: 4px;\n\tborder: 1px solid #BDBDBD;\n\theight: 40px;\n}\n\n.font-name {\n\twidth: 554px;\n}\n\n.font-weight {\n\twidth: 140px;\n}\n\n.font-size {\n\twidth: 48px;\n}\n\n.font-color-text {\n\twidth: 90px;\n}\n\n.font-color {\n\twidth: 48px;\n}\n</style>\n"],"sourceRoot":""}]);
